	module.exports = {"pathContext":{"docHtml":"<h1>url-parse-lax <a href=\"https://travis-ci.org/sindresorhus/url-parse-lax\"><img src=\"https://travis-ci.org/sindresorhus/url-parse-lax.svg?branch=master\" alt=\"Build Status\"></a></h1>\n<blockquote>\n<p><a href=\"https://nodejs.org/docs/latest/api/url.html#url_url_parse_urlstr_parsequerystring_slashesdenotehost\"><code>url.parse()</code></a> with support for protocol-less URLs &#x26; IPs</p>\n</blockquote>\n<h2>Install</h2>\n<pre><code>$ npm install --save url-parse-lax\n</code></pre>\n<h2>Usage</h2>\n<pre><code class=\"language-js\">var urlParseLax = require('url-parse-lax');\n\nurlParseLax('sindresorhus.com');\n/*\n{\n    protocol: null,\n    slashes: true,\n    auth: null,\n    host: 'sindresorhus.com',\n    port: null,\n    hostname: 'sindresorhus.com',\n    hash: null,\n    search: null,\n    query: null,\n    pathname: '/',\n    path: '/',\n    href: 'http://sindresorhus.com/'\n}\n*/\n\nurlParseLax('[2001:db8::]:8000');\n/*\n{\n    protocol: null,\n    slashes: true,\n    auth: null,\n    host: '[2001:db8::]:8000',\n    port: '8000',\n    hostname: '2001:db8::',\n    hash: null,\n    search: null,\n    query: null,\n    pathname: '/',\n    path: '/',\n    href: 'http://[2001:db8::]:8000/'\n}\n*/\n</code></pre>\n<p>And with the built-in <code>url.parse()</code>:</p>\n<pre><code class=\"language-js\">var url = require('url');\n\nurl.parse('sindresorhus.com');\n/*\n{\n    protocol: null,\n    slashes: null,\n    auth: null,\n    host: null,\n    port: null,\n    hostname: null,\n    hash: null,\n    search: null,\n    query: null,\n    pathname: 'sindresorhus',\n    path: 'sindresorhus',\n    href: 'sindresorhus'\n}\n*/\n\nurl.parse('[2001:db8::]:8000');\n/*\n{\n    protocol: null,\n    slashes: null,\n    auth: null,\n    host: null,\n    port: null,\n    hostname: null,\n    hash: null,\n    search: null,\n    query: null,\n    pathname: '[2001:db8::]:8000',\n    path: '[2001:db8::]:8000',\n    href: '[2001:db8::]:8000'\n}\n*/\n</code></pre>\n<h2>License</h2>\n<p>MIT © <a href=\"http://sindresorhus.com\">Sindre Sorhus</a></p>","docPath":"argo-ci/node_modules/url-parse-lax/readme","proj":"argo-ci"}}

